import axios from "axios"
import { useState, useRef } from "react"
import FileBase from 'react-file-base64'
import swal from "sweetalert"

export default function FormImage(){

const[tags, setTags]= useState([]);
const[title, setTitle]= useState("");
const[description, setDescription]= useState("");
const[creator, setCreator]= useState("");
const[selected, setSelected]= useState(null);

const handleTitleChange = ({ target: {value } }) => setTitle(value);
const handleDescriptionChange = ({ target: {value } }) => setDescription(value);
const handleCreatorChange = ({ target: {value } }) => setCreator(value);
const handleTagsChange = ({ target: {value } }) => setTags(value);
/*const handleSelectedChange = ({ target: {value } }) => setSelected(value);*/
const handleFileChange = (e) => {
    setSelected(e)
}
const inputFileRef = useRef()

/*const information = {
    title:title,
    description:description,
    creator:creator,
    tags:tags,
    
}
console.log(information)*/
const information = new FormData()
information.append('title', title)
information.append('description', description)
information.append('creator', creator)
information.append('tags', tags)
information.append('selectedFile', selected)

const url="https://shiyogallery-backend.herokuapp.com/posts/"
const handleEventPost = async()=>{
    await axios.post(url, information)
    .then(response=>{
        console.log(response.data)
        swal({
            title: "Subido",
            text: "Imagen subida correctamente",
            icon: "success",
            button: "Aceptar",
            timer: "5000"
        })
        window.location.href="../"
    })
    .catch(error=>{
        console.log(error)
    })
}

    return(
    <div>
        <div className="">
            <div className="min-h-screen bg-purple-800 flex flex-col justify-center">
                <div className="max-w-md w-full mx-auto">
                    <div className="text-3xl font-bold text-gray-900 mt-2 text-center">Contactame</div>
                </div>
                <div className="max-w-md w-full mx-auto mt-4 bg-white p-8 border-gray-300 shadow-lg">
                 <form className="space-y-6">
                     <div>
                        <label className="text-sm font-bold text-gray-100 block" for="userInput">Creador</label>
                        <input className="w-full p-2 border border-gray-300 rounded mt-1" value={creator} type="text" id="creatorInput" onChange={handleCreatorChange}></input>
                     </div>
                    <div>
                        <label className="text-sm font-bold text-gray-100 block" for="titleInput">Titulo</label>
                        <input className="w-full p-2 border border-gray-300 rounded mt-1" value={title} type="text" id="titleInput" onChange={handleTitleChange}></input>
                    </div>
                    <div>
                        <label className="text-sm font-bold text-gray-100 block" for="descriptionInput">Descripción</label>
                        <textarea className="w-full p-2 border border-gray-300 rounded mt-1" value={description} type="text" id="descriptionInput" onChange={handleDescriptionChange}></textarea>
                    </div>
                    <div>
                        <label className="text-sm font-bold text-gray-100 block" for="generoInput">Categoria</label>
                        <input className="w-full p-2 border border-gray-300 rounded mt-1" value={tags} type="text" id="generoInput" onChange={handleTagsChange}></input>
                    </div>
                    <div>
                        <label className="text-sm font-bold text-gray-100 block" for="fileInput">Subir imagen</label>
                        <input className="" ref={inputFileRef} type="file" id="fileInput" onChange={(e)=>handleFileChange(e.target.files[0])} /> 
                    </div>
                    <div>
                    <a className="w-full py-2 px-4 bg-blue-500 hover:bg-blue-400 rounded-md text-white text-sm cursor-pointer" onClick={handleEventPost}>Agregar</a>
                    </div>         
                 </form>
                </div>
            </div>
        </div>
    </div>
    )
}